<template>
  <div class="add-task">
    <h3>Agregar Tarea</h3>
    <form @submit.prevent="guardarTarea">
      <!-- Campo para el título de la tarea -->
      <input
        v-model="task.title"
        type="text"
        placeholder="Título de la tarea"
        required
      />

      <!-- Campo para la descripción de la tarea -->
      <textarea
        v-model="task.description"
        placeholder="Descripción de la tarea"
      ></textarea>

      <!-- Botón para agregar la tarea -->
      <button type="submit">Agregar Tarea</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'AgregarTarea',
  data() {
    return {
      task: {
        title: '',
        description: '',
      },
    };
  },
  methods: {
    async guardarTarea() {
      // Verifica que el título no esté vacío antes de hacer la solicitud
      if (!this.task.title.trim()) return;

      try {
        // Realiza la llamada POST al API con la tarea
        await axios.post('https://dummyjson.com/todos', {
          title: this.task.title,
          description: this.task.description,
        });
        console.log('Tarea guardada con éxito');
        
        // Limpia los campos después de guardar
        this.task.title = '';
        this.task.description = '';
      } catch (error) {
        console.error('Error al guardar la tarea:', error);
      }
    },
  },
};
</script>

<style scoped>
/* Estilos para el formulario de agregar tareas */
.add-task {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

input,
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}
</style>
